import React from "react"
import { Route, Switch } from "react-router-dom"

const HomePage = () => <div>home</div>
const PowerPlantDetailPage = () => <div>power plant detail</div>

function App() {
  return (
    <div style={{ margin: 20 }}>
      <h5>Time zone: {process.env.REACT_APP_TIME_ZONE}</h5>

      <Switch>
        <Route path="/plants/:plantId" component={PowerPlantDetailPage} />
        <Route component={HomePage} />
      </Switch>

      <div style={{ paddingBottom: 200 }} />
    </div>
  )
}

export default App
